/* Your custom code goes here! */
.ca-container {
  max-width: 1205px;
  margin: 0 auto;
  padding: 0 20px;
}

.ca-block-text-image {
  padding-bottom: 60px;
}
.ca-block-text-image__content {
  background-color: #EBEEF2;
  padding: 50px 0;
}
.ca-block-text-image__image {
  display: block;
  margin: 30px auto;
}

.ca-block-faqs {
  position: relative;
  padding: 60px 0;
}
.ca-block-faqs__question-mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 220px;
  height: 400px;
  opacity: 0;
  transform: translate(-140px, -140px);
  transition: 1500ms ease;
}
@media (min-width: 991px) {
  .ca-block-faqs__question-mark {
    transform: translate(-140px, -120px);
  }
}
.ca-block-faqs__question-mark.is-inviewport {
  opacity: 0.1;
  transform: translate(-70px, -140px);
  transition-delay: 500ms;
}
@media (min-width: 991px) {
  .ca-block-faqs__question-mark.is-inviewport {
    transform: translate(-70px, -120px);
  }
}
.ca-block-faqs .ca-container {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 767px) {
  .ca-block-faqs .ca-container {
    flex-direction: row;
  }
}
.ca-block-faqs__head {
  position: relative;
  width: 100%;
}
@media (min-width: 767px) {
  .ca-block-faqs__head {
    width: 30%;
  }
}
.ca-block-faqs__main {
  width: 100%;
}
.ca-block-faqs__main .ca-block-faqs__title:first-child {
  padding-top: 0;
}
@media (min-width: 767px) {
  .ca-block-faqs__main {
    width: 70%;
    margin-left: auto;
  }
}
@media (min-width: 767px) {
  .ca-block-faqs__heading {
    font-size: 100px;
  }
}
.ca-block-faqs__title {
  display: block;
  padding: 40px 0 10px;
}
.ca-block-faqs__question {
  font-size: 18px;
}
.ca-block-faqs .ca-accordion {
  border-bottom: 1px solid #EBEEF2;
  margin-bottom: 20px;
}

.ca-block-benefits-list {
  padding: 50px 0;
  background: #15aae0;
  background: linear-gradient(90deg, #052642 0%, #15aae0 100%);
}
.ca-block-benefits-list__wrap {
  display: flex;
  flex-direction: column;
}
@media (min-width: 767px) {
  .ca-block-benefits-list__wrap {
    flex-direction: row;
  }
}
.ca-block-benefits-list__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #FFF;
  width: 100%;
  padding: 25px 25px 0;
}
@media (min-width: 767px) {
  .ca-block-benefits-list__inner {
    width: 50%;
  }
}
.ca-block-benefits-list__heading {
  color: #FFF;
}
.ca-block-benefits-list__list {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 25px;
}
@media (min-width: 767px) {
  .ca-block-benefits-list__list {
    width: 50%;
  }
}
.ca-block-benefits-list__list-item {
  display: flex;
  align-items: center;
  color: #FFF;
  padding-bottom: 15px;
}
@media (min-width: 767px) {
  .ca-block-benefits-list__list-item {
    padding-left: 80px;
  }
}
.ca-block-benefits-list__list-item svg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  transition: 500ms ease;
}
.ca-block-benefits-list__list-item svg circle,
.ca-block-benefits-list__list-item svg .path-1,
.ca-block-benefits-list__list-item svg .path-2 {
  transition: 500ms ease;
}
.ca-block-benefits-list__list-item:hover svg circle {
  transform: translate(-4px, -4px);
}
.ca-block-benefits-list__list-item:hover svg .path-1 {
  stroke: #15aae0;
}
.ca-block-benefits-list__list-item:hover svg .path-2 {
  stroke: #052642;
}

.ca-block-documentation {
  padding: 60px 0;
}
.ca-block-documentation__wrapper {
  max-width: 700px;
  width: 100%;
}
.ca-block-documentation__link-items {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.ca-block-documentation__link-item {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 15px;
  -webkit-box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.1);
  -moz-box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.1);
  box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.1);
  border: 1px solid #C3C3C3;
  border-radius: 5px;
  transition: 350ms ease;
}
.ca-block-documentation__link-item:hover {
  border-color: #052642;
  -webkit-box-shadow: 0px 20px 40px 0px rgba(5, 38, 66, 0.5);
  -moz-box-shadow: 0px 20px 40px 0px rgba(5, 38, 66, 0.5);
  box-shadow: 0px 20px 40px 0px rgba(5, 38, 66, 0.5);
}
@media (min-width: 767px) {
  .ca-block-documentation__link-item {
    flex-direction: row;
    align-items: center;
  }
}
.ca-block-documentation__title {
  font-size: 16px;
  color: #052642;
}
.ca-block-documentation__link-image-wrap {
  width: 85px;
  display: flex;
  gap: 5px;
}
.ca-block-documentation__link-image-inner {
  width: 40px;
  height: 40px;
  padding: 5px;
  border: 1px solid #C3C3C3;
  border-radius: 5px;
}
.ca-block-documentation__link-image {
  width: 40px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.ca-block-documentation__description {
  margin-bottom: 0;
  font-size: 14px;
}
.ca-block-documentation__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  text-align: center;
  padding: 8px 20px;
  background-color: #052642;
  color: #FFF;
  height: fit-content;
  border-radius: 3px;
  transition: 350ms ease;
}
.ca-block-documentation__link:hover {
  background-color: #15aae0;
  color: #FFF;
}
@media (min-width: 767px) {
  .ca-block-documentation__link {
    margin-left: auto;
    display: inline-block;
  }
}
.ca-block-documentation__link-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ca-accordion__trigger {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.ca-accordion__trigger.active svg {
  transform: rotate(-90deg);
}
.ca-accordion__icon {
  margin-left: 16px;
  transform: rotate(90deg);
  transition: 500ms ease;
}
.ca-accordion__content {
  display: none;
}
.ca-accordion__content.active {
  display: block;
}

[data-inviewport=fade-in-left] {
  opacity: 0;
  transform: translateX(28px);
}
[data-inviewport=fade-in-left].is-inviewport {
  opacity: 1;
  transform: translateX(0);
  transition: 1000ms ease;
  transition-delay: 150ms;
}

[data-inviewport=fade-in-right] {
  opacity: 0;
  transform: translateX(-28px);
}
[data-inviewport=fade-in-right].is-inviewport {
  opacity: 1;
  transform: translateX(0);
  transition: 1000ms ease;
  transition-delay: 150ms;
}

[data-inviewport=fade-down] {
  opacity: 0;
  transform: translateY(-28px);
}
[data-inviewport=fade-down].is-inviewport {
  opacity: 1;
  transform: translateY(0);
  transition: 1000ms ease;
  transition-delay: 150ms;
}

[data-inviewport=fade-up] {
  opacity: 0;
  transform: translateY(28px);
}
[data-inviewport=fade-up].is-inviewport {
  opacity: 1;
  transform: translateY(0);
  transition: 1000ms ease;
  transition-delay: 150ms;
}

[data-inviewport=fade-in] {
  opacity: 0;
}
[data-inviewport=fade-in].is-inviewport {
  opacity: 1;
  transition: 1000ms ease;
  transition-delay: 150ms;
}

.ca-single {
  margin-top: 22px;
}

.ca-single-pms-partner-integration__wrapper {
  display: flex;
  align-items: start;
  gap: 25px;
}
@media (min-width: 767px) {
  .ca-single-pms-partner-integration__wrapper {
    flex-direction: row;
  }
}
.ca-single-pms-partner-integration__content {
  width: 100%;
}
@media (min-width: 620px) {
  .ca-single-pms-partner-integration__content {
    width: 50%;
  }
}
.ca-single-pms-partner-integration__header {
  padding: 50px 0;
}
.ca-single-pms-partner-integration__title {
  margin: 0;
  padding-bottom: 16px;
  color: #052642;
  font-size: 31px;
}
@media (min-width: 767px) {
  .ca-single-pms-partner-integration__title {
    font-size: 39px;
  }
}
.ca-single-pms-partner-integration__integrated-type {
  color: #15aae0;
  font-size: 18px;
}
.ca-single-pms-partner-integration__integrated-type-name {
  display: block;
  color: #54595F;
  line-height: 18px;
}
.ca-single-pms-partner-integration__thumbnail {
  margin-left: auto;
  width: 75px;
  height: 75px;
  background-color: #FFF;
  border-radius: 25px;
  overflow: hidden;
  -webkit-box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.5);
  -moz-box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.5);
  box-shadow: 0px 20px 40px -19px rgba(5, 38, 66, 0.5);
}
@media (min-width: 767px) {
  .ca-single-pms-partner-integration__thumbnail {
    width: 150px;
    height: 150px;
  }
}
.ca-single-pms-partner-integration__thumbnail__image {
  width: 75px;
  height: 75px;
  object-fit: contain;
}
@media (min-width: 767px) {
  .ca-single-pms-partner-integration__thumbnail__image {
    width: 150px;
    height: 150px;
  }
}

.ca-more-integration {
  padding: 60px 0;
  background-color: #052642;
}
.ca-more-integration__heading {
  color: #FFF;
  padding-bottom: 40px;
}
.ca-more-integration__items {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.ca-more-integration__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #FFF;
  padding: 25px;
  border-radius: 15px;
}
@media (min-width: 767px) {
  .ca-more-integration__item {
    flex-basis: calc(50% - 20px);
  }
}
@media (min-width: 991px) {
  .ca-more-integration__item {
    flex-basis: calc(33.333% - 20px);
  }
}
.ca-more-integration__item:hover {
  transition: 350ms ease !important;
  -webkit-box-shadow: 0px 3px 50px 10px rgba(255, 255, 255, 0.2);
  -moz-box-shadow: 0px 3px 50px 10px rgba(255, 255, 255, 0.2);
  box-shadow: 0px 3px 50px 10px rgba(255, 255, 255, 0.2);
}
.ca-more-integration__item-image {
  padding: 0 25px 25px 0;
  width: 100px;
  height: 100px;
  overflow: hidden;
  border-radius: 25px;
}
.ca-more-integration__item-image img {
  width: 75px;
  height: 75px;
  object-fit: contain;
}
.ca-more-integration__title {
  color: #052642;
}
.ca-more-integration__link {
  margin-top: auto;
  color: #15aae0;
  transition: 350ms ease;
}
.ca-more-integration__link:hover {
  color: #052642;
}

.ca-why-charge-automation {
  padding: 40px 0;
}
.ca-why-charge-automation__content ul, .ca-why-charge-automation__content ol {
  padding-left: 40px;
}
@media (min-width: 767px) {
  .ca-why-charge-automation__content ul, .ca-why-charge-automation__content ol {
    padding-left: 80px;
  }
}